import React from 'react';

// reactstrap components
import { Button, Container, Row, Col, UncontrolledTooltip } from 'reactstrap';
// core components

function IndexHeader() {
  let pageHeader = React.createRef();

  React.useEffect(() => {
    if (window.innerWidth > 991) {
      const updateScroll = () => {
        let windowScrollTop = window.pageYOffset / 3;
        pageHeader.current.style.transform =
          'translate3d(0,' + windowScrollTop + 'px,0)';
      };
      window.addEventListener('scroll', updateScroll);
      return function cleanup() {
        window.removeEventListener('scroll', updateScroll);
      };
    }
  });

  return (
    <>
      <div className='page-header clear-filter' filter-color='blue'>
        <div
          className='page-header-image'
          style={{
            backgroundImage: 'url(' + require('assets/img/header.jpg') + ')'
          }}
          ref={pageHeader}
        ></div>
        <Container>
          <div className='content-center brand'>
            <img
              alt='...'
              className='rounded-circle img-raised'
              src={require('assets/img/now-logo.png')}
              width='260'
              height='260'
            ></img>
            <br />
            <br />
            <h1 className='h1-seo'>Hello! I'm Warinya</h1>
            <h3>A front end focused developer with a fullstack experience.</h3>

            <Row className='justify-content-md-center sharing-area text-center'>
              <Col className='text-center' lg='8' md='12'></Col>
              <Col className='text-center' lg='8' md='12' id='contact-section'>
                <Button
                  className='btn-neutral btn-icon btn-round'
                  color='email'
                  href='mailto:wariroja@gmail.com'
                  id='tooltip86114138'
                  size='lg'
                  target='_blank'
                >
                  <i className='now-ui-icons ui-1_email-85'></i>
                </Button>
                <Button
                  className='btn-neutral btn-icon btn-round'
                  color='github'
                  href='https://www.github.com/wariroja'
                  id='tooltip735272548'
                  size='lg'
                  target='_blank'
                >
                  <i className='fab fa-github'></i>
                </Button>
                <Button
                  className='btn-neutral btn-icon btn-round'
                  color='linkedin'
                  href='https://www.linkedin.com/in/wariroja'
                  id='tooltip647117716'
                  size='lg'
                  target='_blank'
                >
                  <i className='fab fa-linkedin'></i>
                </Button>
                <Button
                  className='btn-neutral btn-icon btn-round'
                  color='github'
                  href='https://www.medium.com/@wariroja'
                  id='tooltip331904895'
                  size='lg'
                  target='_blank'
                >
                  <i className='fab fa-medium'></i>
                </Button>
              </Col>
            </Row>
          </div>
        </Container>
      </div>
    </>
  );
}

export default IndexHeader;
