/** @format */

import React from 'react';
import { Link } from 'react-router-dom';
// reactstrap components
import {
  Button,
  Collapse,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
  NavbarBrand,
  Navbar,
  NavItem,
  NavLink,
  Nav,
  Container,
  UncontrolledTooltip
} from 'reactstrap';

function IndexNavbar() {
  const [navbarColor, setNavbarColor] = React.useState('navbar-transparent');
  const [collapseOpen, setCollapseOpen] = React.useState(false);
  React.useEffect(() => {
    const updateNavbarColor = () => {
      if (
        document.documentElement.scrollTop > 399 ||
        document.body.scrollTop > 399
      ) {
        setNavbarColor('');
      } else if (
        document.documentElement.scrollTop < 400 ||
        document.body.scrollTop < 400
      ) {
        setNavbarColor('navbar-transparent');
      }
    };
    window.addEventListener('scroll', updateNavbarColor);
    return function cleanup() {
      window.removeEventListener('scroll', updateNavbarColor);
    };
  });
  return (
    <>
      {collapseOpen ? (
        <div
          id='bodyClick'
          onClick={() => {
            document.documentElement.classList.toggle('nav-open');
            setCollapseOpen(false);
          }}
        />
      ) : null}
      <Navbar className={'fixed-top ' + navbarColor} expand='lg' color='info'>
        <Container>
          <div className='navbar-translate'>
            <NavbarBrand href='' target='_blank' id='navbar-brand'>
              Warinya Rojanasuwan
            </NavbarBrand>

            <button
              className='navbar-toggler navbar-toggler'
              onClick={() => {
                document.documentElement.classList.toggle('nav-open');
                setCollapseOpen(!collapseOpen);
              }}
              aria-expanded={collapseOpen}
              type='button'
            >
              <span className='navbar-toggler-bar top-bar'></span>
              <span className='navbar-toggler-bar middle-bar'></span>
              <span className='navbar-toggler-bar bottom-bar'></span>
            </button>
          </div>
          <Collapse
            className='justify-content-end'
            isOpen={collapseOpen}
            navbar
          >
            <Nav navbar>
              <NavItem>
                <NavLink
                  href='#pablo'
                  onClick={e => {
                    e.preventDefault();
                    document
                      .getElementById('projects-section')
                      .scrollIntoView();
                  }}
                >
                  <i className='now-ui-icons sport_trophy'></i>
                  <p>Projects</p>
                </NavLink>
              </NavItem>
              <NavItem>
                <NavLink
                  href='#pablo'
                  onClick={e => {
                    e.preventDefault();
                    document
                      .getElementById('download-section')
                      .scrollIntoView();
                  }}
                >
                  <i className='now-ui-icons ui-2_favourite-28'></i>
                  <p>About Me</p>
                </NavLink>
              </NavItem>
              {/* <UncontrolledDropdown nav>
                <DropdownToggle
                  caret
                  color="default"
                  href="#pablo"
                  nav
                  onClick={e => e.preventDefault()}
                >
                  <i className="now-ui-icons design_app mr-1"></i>
                  <p>Components</p>
                </DropdownToggle>
                <DropdownMenu>
                  <DropdownItem to="/index" tag={Link}>
                    <i className="now-ui-icons business_chart-pie-36 mr-1"></i>
                    All components
                  </DropdownItem>
                  <DropdownItem
                    href="https://demos.creative-tim.com/now-ui-kit-react/#/documentation/introduction?ref=nukr-index-navbar"
                    target="_blank"
                  >
                    <i className="now-ui-icons design_bullet-list-67 mr-1"></i>
                    Documentation
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown> */}
              {/* <NavItem>
                <Button
                  className='nav-link btn-neutral'
                  color='info'
                  href='contact-section'
                  id='upgrade-to-pro'
                  onClick={e => {
                    e.preventDefault();
                    document.getElementById('contact-section').scrollIntoView();
                  }}
                >
                  <i className='now-ui-icons emoticons_satisfied'></i>{' '}
                  <p>Contact</p>
                </Button>
                <UncontrolledTooltip target='#upgrade-to-pro'>
                  Let's Chat!
                </UncontrolledTooltip>
              </NavItem> */}
              <NavItem>
                <NavLink
                  href='https://www.github.com/wariroja'
                  target='_blank'
                  id='twitter-tooltip'
                >
                  <i className='fab fa-github'></i>
                  <p className='d-lg-none d-xl-none'>Github</p>
                </NavLink>
                <UncontrolledTooltip target='#twitter-tooltip'>
                  Check out my Github!
                </UncontrolledTooltip>
              </NavItem>
              <NavItem>
                <NavLink
                  href='https://www.linkedin.com/in/wariroja/'
                  target='_blank'
                  id='facebook-tooltip'
                >
                  <i className='fab fa-linkedin'></i>
                  <p className='d-lg-none d-xl-none'>Linkedin</p>
                </NavLink>
                <UncontrolledTooltip target='#facebook-tooltip'>
                  Connect with me on Linkedin
                </UncontrolledTooltip>
              </NavItem>
              <NavItem>
                <NavLink
                  href='mailto:wariroja@gmail.com'
                  target='_blank'
                  id='instagram-tooltip'
                >
                  <i className='now-ui-icons ui-1_email-85'></i>
                  <p className='d-lg-none d-xl-none'>Email</p>
                </NavLink>
                <UncontrolledTooltip target='#instagram-tooltip'>
                  Let's create something!
                </UncontrolledTooltip>
              </NavItem>
            </Nav>
          </Collapse>
        </Container>
      </Navbar>
    </>
  );
}

